import {useState} from "react";
import './App.css';


const App=()=>{
  const [users,setUser] = useState([]);

  const loadUsers=async()=>{
    const response = await fetch ("https://reqres.in/api/users?page=1").then((response)=>response.json()).then((res)=>{setUser(res.data)});
    
  }

  return(
    <>
    
    <div className="App">

    
  <div className="Users">
    <div class="logo">ZIPPY</div>
      <button className="button" onClick={loadUsers}> Show All Users</button></div>
      
      
      
      <ul className="Users">
        
        {users.map(({id,first_name,last_name,email,avatar})=>(
          <div className="Userdata" key={id}>
            <img  src={avatar}></img><br/>
            <span className="name">{first_name} {last_name}<br/> </span>
            <span className="email">{email}</span>
            <br/><br/><br/><br/>
            </div>
            ))}
          
      </ul>
      
    </div>
    
    </>
  )
}
   
export default App;
